.contact {
    background-color: white;
    display: flex;
}

.contact include mobile {
    flex-direction: column;
}

.contact .left {
    flex: 1;
    overflow: hidden;
}

.contact .left img {
    height: 100%;
}

.contact .right {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.contact .right h2 {
    font-size: 30px;
}

.contact .right form {
    width: 70%;
    height: 70%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
}

.contact .right form input {
    width: 300px;
    height: 30px;
    font-size: 14px;
}

.contact .right form input include mobile {
    width: 200px;
    height: 20px;
}

.contact .right form textarea {
    width: 300px;
    height: 200px;
    font-size: 14px;
}

.contact .right form textarea include mobile {
    width: 200px;
    height: 100px;
}

.contact .right form button {
    width: 150px;
    height: 30px;
    color: white;
    background-color: crimson;
    border: none;
    border-radius: 10px;
    font-weight: 500;
    cursor: pointer;
}

.contact .right form button :focus {
    outline: none;
}

.contact .right form span {
    color: green;
  }