.intro {
    background-color: white;
    display: flex;
}

.intro .left {
    flex: 0.5;
    overflow: hidden;
}

.intro .left .imgContainer {
    width: 700px;
    height: 700px;
    background-color: #df0505;
    border-radius: 50%;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    float: right;
}

.intro .left .imgContainer img{
    height: 90%;
}

.intro .right {
    flex: 0.5;
    position: relative;
}

.intro .right .wrapper {
    width: 100%;
    height: 100%;
    padding-left: 50px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.intro .right .wrapper h1 {
    font-size: 60px;
    margin: 10px 0;
    }

  
    .intro .right .wrapper h2 {
    font-size: 35px;
  }

  .intro .right .wrapper h3 {
    font-size: 30px;
  }

  .intro .right .wrapper a{
      position: absolute;
      bottom: 10px;
      left: 40%;
  }

  .intro .right .wrapper span{
    font-size: 30px;
    color: #df0505;
}

.itype-cursor {
  animation: blink 1s infinite;
}

@keyframes blink {
  50%{
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

  .intro .right .wrapper img {
      width: 30px;
      animation: arrowBlink 2s infinite;
  }

  @keyframes arrowBlink {
    100% {
      opacity: 0;
    }
  }


