.works {
    background-color: crimson;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
}

.arrow {
    height: 50px;
    position: absolute;
    cursor: pointer;
  }
  .arrow.left {
    left: 100px;
    transform: rotateY(180deg);
  }
  .arrow.right {
    right: 100px;
  }
  

.works .slider {
    height: 350px;
    display: flex;
    position: absolute;
    left: 0;
    transition: all 1s ease-out;
}

.works .slider .container {
    width: 100vw;
    display: flex;
    align-items: center;
    justify-content: center;
}

.works .slider .container .item {
    width: 700px;
    height: 100%;
    background-color: white;
    border-radius: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.works .slider .container .item .left {
    flex: 4;
    height: 80%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.works .slider .container .item .left .leftContainer {
    width: 90%;
    height: 70%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.works .slider .container .item .left .leftContainer .imgContainer {
              width: 40px;
              height: 40px;
              border-radius: 50%;
              background-color: rgb(245, 179, 155);
              display: flex;
              align-items: center;
              justify-content: center;
}

.works .slider .container .item .left .leftContainer .imgContainer img {
        width: 25px;
}   

.works .slider .container .item .left .leftContainer .imgContainer img  include mobile {
    width: 15px;
  }

.works .slider .container .item .left .leftContainer h2 {
        font-size: 20px;
}

.works .slider .container .item .left .leftContainer h2 include mobile {
    font-size: 13px;
  }

.works .slider .container .item .left .leftContainer p {
    font-size: 13px;
}

.works .slider .container .item .left .leftContainer p include mobile {
    display: none;
}

.works .slider .container .item .left .leftContainer span {
    font-size: 12px;
    font-weight: 600;
    text-decoration: underline;
    cursor: pointer;
}
    
.works .slider .container .item .right {
    flex: 8;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
}

.works .slider .container .item .right img {
    width: 400px;
    transform: rotate(-10deg);
}



